<template>
  <div class="mission-note">
    <div class="mission-box">
      <div class="mission-head">
        <div @click="showMission" :class="['mission-head-content', {'mission-active': !showTarget}]">任務目標</div>
        <div @click="showNote" :class="['mission-head-content', {'mission-active': showTarget, 'mission-dot': isDotNoteHead }]">館長筆記</div>
      </div>
      <div v-if="showTarget" class="mission-target">
        <div>世界上本來就存在著怪奇，他們擁有著神祕的力量，而負責管理怪奇的怪奇博物館近日遭到攻擊，導致怪奇出逃了！<br><br>
          請依照館長的給予的線索，幫忙捉回等級最高的 4 隻怪奇吧！</div>
      </div>
      <div v-else class="mission-man-note">
        <div :class="['mission-note-head', {'mission-number-dot-1': isDotNoteHeadOne, 'mission-number-dot-2': isDotNoteHeadTwo}]">
          <img @click="showContentOne" class='mission-note-number' src='@/assets/images/one.png' alt='one'>
          <img @click="showContentTwo" class='mission-note-number' src='@/assets/images/two.png' alt='two'>
          <img @click="showContentThree" class='mission-note-number' src='@/assets/images/three.png' alt='three'>
          <img @click="showContentFour" class='mission-note-number' src='@/assets/images/four.png' alt='four'>
        </div>
        <div class="mission-note-content">
          <div
            class='mission-note-papper'
            :style="{ backgroundImage: 'url(' + papperBg + ')' }"
          >
            <div class="mission-note-papper-number">{{ papperNumber }}</div>
            <div class="mission-note-papper-text">
              <!-- 3 -->
              <img v-if="papperNumber==='3'" class='mission-note-ladder' src='@/assets/images/commute/commute-for-note.png' alt='one'>
              <div v-html="papperContent"></div>
              <!-- 1 -->
              <div @click="goToLingVideo" v-if="papperNumber==='1' && isLingWin" class="mission-see-back">一窺從前</div>
              <!-- 2 -->
              <div style="text-align: center;" v-if="papperNumber==='2' && isLuluWin">
                <audio style="width: 80%;" controls>
                  <source src="@/assets/audio/lulu-audio.mp3" type="audio/mpeg">
                  Your browser does not support the audio element.
                </audio>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import papperBg from '@/assets/images/papper.png'

export default {
  name: 'MissionNote',
  components: {

  },
  data () {
    return {
      showTarget: true,
      papperBg: papperBg,
      papperContent: `
      之前追捕這個怪奇卻被擊敗的館員，全身受了非常嚴重的燒傷，目前還陷入昏迷中。<br><br>
      而且更奇怪的是，他的身上竟然長出了⋯⋯長出了樹苗。<br><br>
      昏迷中的他，口中不時喃喃唸著『鬼啊、鬼啊』，似乎陷入惡夢之中。<br><br>
      也許問問附近的民眾『有聽說這裡哪邊鬧鬼或失火嗎？』會是個線索。<br><br>
      天黑黑～風蕭蕭～ 欲落雨～樹搖搖～<br>
      樓上的窗內有火光～ 樓頂的庭院有人影～<br>
      日時親像外國的迪斯可～ 夜時有聲無影在哀哭～
      `,
      papperNumber: '1'
    }
  },
  mounted () {
  },
  methods: {
    showMission () {
      this.showTarget = true
    },
    showNote () {
      this.showTarget = false
      // 關閉 isDotNoteHead
      this.$store.commit('changeNotDotHead', false)
    },
    showContentOne () {
      this.papperContent = `
      之前追捕這個怪奇卻被擊敗的館員，全身受了非常嚴重的燒傷，目前還陷入昏迷中。<br><br>
      而且更奇怪的是，他的身上竟然長出了⋯⋯長出了樹苗。<br><br>
      昏迷中的他，口中不時喃喃唸著『鬼啊、鬼啊』，似乎陷入惡夢之中。<br><br>
      也許問問附近的民眾『有聽說這裡哪邊鬧鬼或失火嗎？』會是個線索。<br><br>
      天黑黑～風蕭蕭～ 欲落雨～樹搖搖～<br>
      樓上的窗內有火光～ 樓頂的庭院有人影～<br>
      日時親像外國的迪斯可～ 夜時有聲無影在哀哭～
      `
      this.papperNumber = '1'
      this.$store.commit('changeNotDotHeadOne', false)
    },
    showContentTwo () {
      this.papperContent = `
        偵查的館員報告，某個地方出現了『怪物』，是基隆市民、市鳥與市魚的綜合樣貌，會問經過的旅人奇怪的問題，有館員回報如果能力不足或答錯太多，就會憤怒地吞噬旅人。在我們派出戰鬥小隊後，卻找不到任何痕跡，可能是怪奇的能力在影響。傳聞這附近還有個奇怪的女人，好像不要激怒她比較好⋯⋯      
      `
      this.papperNumber = '2'
      this.$store.commit('changeNotDotHeadTwo', false)
    },
    showContentThree () {
      this.papperContent = `
      火車哐噹 踅踅去～<br>
      搬車搬到 天烏去～<br>
      搖搖幌幌 睏到昏昏去～<br>
      到位醒來 暗時睏袂去～<br>
      噢，照片裡的傢伙是「物件類怪奇」，平常是一個裝置的模樣，算是我們在這個城市的線人，幫我們監控是否有異常的怪力變化，他也許會有潛伏在此的怪奇情報。他已經在結界定位點等候了，你到他面前拿出怪奇羅盤，破解了密碼他就會現身了。
      `
      this.papperNumber = '3'
    },
    showContentFour () {
      this.papperContent = `
      白鷺鷥車畚箕～車到崁仔頂～<br>
      跋一倒，淹佇臭水溝仔爬袂起～<br>
      抓到吃垃圾的烏鯽仔～<br>
      閣有食魚腸仔的烏鷹鳥呀～烏鷹鳥～
      `
      this.papperNumber = '4'
    },
    goToLingVideo () {
      window.open('https://youtu.be/bBj3wG785-M')
    }
  },
  computed: {
    isLingWin () {
      return this.$store.state.isLingWin
    },
    isLuluWin () {
      return this.$store.state.isLuluWin
    },
    isDotNoteHead () {
      return this.$store.state.isDotNoteHead
    },
    isDotNoteHeadOne () {
      return this.$store.state.isDotNoteHeadOne
    },
    isDotNoteHeadTwo () {
      return this.$store.state.isDotNoteHeadTwo
    }
  }
}
</script>
<style lang="scss" scoped>
  .mission-note {
    min-height: 100vh;
    background: linear-gradient(#000 70%, #95ff00);
    padding-top: 100px;

    .mission {

      &-box {
        width: 80%;
        margin: auto;
      }

      &-head {
        display: flex;
      }

      &-head-content {
        width: 50%;
        height: 50px;
        background-color: #95ff00;
        line-height: 50px;
        text-align: center;
        font-weight: bold;
        border-top-left-radius: 16px;
        border-top-right-radius: 16px;
      }

      &-active {
        background-color:  rgba(149, 255, 0, 0.5);
      }

      &-dot {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: 6px;
          left: 6px;
          width: 18px;
          height: 18px;
          border-radius: 100%;
          background-color: orange;
        }
      }

      &-target {
        min-height: 320px;
        padding: 20px;
        color: #000;
        background-color: rgba(255,255,255,0.7);
      }

      &-target-text {
        margin-top: 20px;
        padding-bottom: 20px;
        border-bottom: 0.5px solid #000;

        &:last-child {
          border-bottom: 0px;
        }
      }

      &-man-note {
        padding: 10px;
        color: #000;
        background-color: rgba(255,255,255,0.7);
      }

      &-note-head {
        display: flex;
        justify-content: space-evenly;
      }

      &-number-dot-1 {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: 23px;
          left: 18%;
          width: 18px;
          height: 18px;
          border-radius: 100%;
          background-color: orange;
        }
      }

      &-number-dot-2 {
        position: relative;

        &::after {
          content: '';
          position: absolute;
          top: 23px;
          left: 41%;
          width: 18px;
          height: 18px;
          border-radius: 100%;
          background-color: orange;
        }
      }

      &-note-number {
        width: 30px;
        height: 35px;
      }

      &-note-commute-img {
        width: 60px;
        height: auto;
      }

      &-note-content {
        margin-top: 6px;
      }

      &-note-papper {
        width: 100%;
        height: 250px;
        background-size: cover;
        background-position-y: center;
        background-repeat: no-repeat;
        background-position-x: center;
        padding: 20px 5px 40px;
      }

      &-note-papper-number {
        text-align: right;
      }

      &-note-papper-text {
        margin: 18px 5% 0px 5%;
        overflow: scroll;
        max-height: 150px;
      }

      &-see-back {
        background: #95ff00;
        border-radius: 30px;
        width: 100px;
        text-align: center;
        margin: 4px auto 0px;
      }

      &-note-ladder {
        width: 50%;
        height: auto;
      }
    }
  }
</style>



